"use client";

import { Button } from "@/components/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { CheckIcon } from "lucide-react";
import { cn } from "@/lib/utils";
import type { ScoreType } from "@/types/album";
import { ScoreType as ScoreEnum } from "@/types/album";

interface FilterButtonsProps {
  selectedFilter: string | null;
  onFilterSelect: (filter: string | null) => void;
}

const filterButtons = [
  { filter: "10", label: "10/10" },
  { filter: "9", label: "9/10" },
  { filter: "8", label: "8/10" },
  { filter: "7", label: "7/10" },
  { filter: "6", label: "6/10" },
  { filter: "5", label: "5/10" },
  { filter: "4", label: "4/10" },
  { filter: "3", label: "3/10" },
  { filter: "2", label: "2/10" },
  { filter: "1", label: "1/10" },
  { filter: "0", label: "0/10" },
  { filter: "NOT GOOD", label: "NOT GOOD" },
  { filter: "OTHER", label: "OTHER SCORES" },
];

export function FilterButtons({
  selectedFilter,
  onFilterSelect,
}: FilterButtonsProps) {
  const selectedLabel = selectedFilter
    ? filterButtons.find((f) => f.filter === selectedFilter)?.label || "Filter"
    : "Filter";

  return (
    <div className="mb-4">
      <DropdownMenu>
        <DropdownMenuTrigger asChild>
          <Button
            variant="outline"
            className="bg-gray-800 text-gray-300 hover:bg-gray-700"
          >
            {selectedLabel}
          </Button>
        </DropdownMenuTrigger>
        <DropdownMenuContent>
          <DropdownMenuItem onClick={() => onFilterSelect(null)}>
            {selectedFilter === null ? (
              <CheckIcon className="size-4 mr-2" />
            ) : (
              <span className="w-4 mr-2" />
            )}{" "}
            Clear Filter
          </DropdownMenuItem>
          {filterButtons.map(({ filter, label }) => (
            <DropdownMenuItem
              key={filter}
              onClick={() => onFilterSelect(filter)}
            >
              {selectedFilter === filter ? (
                <CheckIcon className="size-4 mr-2" />
              ) : (
                <span className="w-4 mr-2" />
              )}{" "}
              {label}
            </DropdownMenuItem>
          ))}
        </DropdownMenuContent>
      </DropdownMenu>
    </div>
  );
}
